.team {
    @include responsive(padding-top, 80, 120, 52);
    
    &__container    {
    }

    &__head {
    }

    .cases {

        &__head {
            @media(max-width:1024px){
                gap: vw(66, 768);
            }
        }
    
        &__title {
        }
    
        &__text {
            margin-top: vw(10);
            min-width: 0;
            text-transform: uppercase;
            @media(max-width:1024px){
                width: 100%;
            }
        }
    
        &__text-one {
            flex-direction: column;
            gap: 0;

            @media(max-width:1024px){
                p:nth-child(2) {
                    margin-left: auto;
                }
            }
        }
    }

    &__platform {
        position: relative;
        margin-top: vw(-260);

        @media(max-width:1024px){
            margin-top: 0;
        }
    }

    &__row {
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: center;
        @include responsive(gap, 36, 16, 5);

        @media(max-width:1024px){
            justify-content: space-between;
        }
    }

    &__button-slider {
        position: relative;
        @include responsive(margin-top, 603, 413, 243);
        @include responsive(width, 100, 100, 44);
        @include responsive(height, 88, 88, 38);
        cursor: pointer;
        z-index: 3;
        &:hover {
            .str {
                &:first-child {
                    animation: fade 1.6s infinite ease-in-out;
                }
                &:last-child {
                    animation: fade 1.6s infinite ease-in-out 0.2s;
                }
            }
        }
        .str {
            @include responsive(width, 18, 18, 8);
            @include responsive(height, 22, 22, 10);
        }
        .bg {
            width: 100%;
            height: auto;
        }
        .mob {
            display: none;
        }
        @media(max-width:480px){
            margin-left: -100%;
            .mob {
                display: block;
            }
            .web {
                display: none;
            }
        }
    }

    &__button-slider-prev {
        &:hover {
            .str {
                &:first-child {
                    animation: fade 1.6s infinite ease-in-out 0.2s;
                }
                &:last-child {
                    animation: fade 1.6s infinite ease-in-out 0s;
                }
            }
        }

        @media(max-width:480px){
            margin-right: -100%;
            margin-left: 0;
        }
    }

    &__button-slider-str {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__slider {
        max-width: vw(562);
        width: 100%;
        overflow: hidden;

        @media(max-width:1024px){
            max-width: vw(460, 768);
        }
        @media(max-width:480px){
            max-width: 100%;
        }
    }

    &__wrapper {
    }

    &__slide {
        transition: all 1.5s ease-in-out 0s;
        opacity: 0;
        &.swiper-slide-active {
            opacity: 1;
        }
    }

    &__button-slider-next {
    }

    &__sup-row {
        position: relative;
        z-index: 1;
        animation: levitation 4s ease-in-out infinite;
        position: relative;
        @include responsive(margin-top, 56, 26, 20);
        display: flex;
        justify-content: center;
        width: 100%;
    }
    &__sup {
        @include responsive(width, 1160, 696, 328);
        @include responsive(height, 194, 116, 55);
        pointer-events: none;
        img {
            width: 100%;
        }
    }
    &__sup-light {
        position: absolute;
        bottom: vw(94);
        left: 50%;
        transform: translateX(-50%);
        @include responsive(width, 836, 696, 328);
        @include responsive(height, 638, 602, 285);
        animation: lightPulse 10s ease-in-out infinite; /* Анимация свечения */

        img {
            width: 100%;
            height: 100%;
        }
    }
    &__pagination {
        display: inline-flex;
        top: auto !important;
        bottom: 0;
        transform: translateX(-50%) translateY(0) !important;
        left: 50% !important;
        width: auto !important;
        bottom: vw(47) !important;

        @media(max-width:1024px){
            bottom: vw(27, 768) !important;
        }
        @media(max-width:480px){
            bottom: vw(12, 360) !important;
        }

        .swiper-pagination-bullet {
            margin: 0 !important;
            background: linear-gradient(359deg, #100e24 0%, #180c25 100%);
            box-shadow: 0 0 6px 0 #100b15, inset 0 2px 3px 0 #000;
        }
        .swiper-pagination-bullet-active {
            background: rgb(163,129,250);
            box-shadow: 0 0 16px  #2602ff;
        }
    }


    &__decor-one {
        position: absolute;
        z-index: 1;
        @include responsive(top, 357, 170, 60);
        @include responsive(left, 310, -70, -35);
        @include responsive(width, 109, 109, 54.5);
        @include responsive(height, 68, 68, 34);
        pointer-events: none;
        img {
            // animation: levitateTwo 10s ease infinite;
            animation: levitateTwo 10s ease-in-out infinite alternate;
            width: 100%;
            height: 100%;
        }
    }
    &__decor-two {
        position: absolute;
        z-index: 1;
        @include responsive(top, 408, 0, 45);
        @include responsive(right, 250, -100, -45);
        @include responsive(width, 194, 194, 96);
        @include responsive(height, 113, 113, 56.5);
        pointer-events: none;
        img {
            animation: levitateTwo 11s ease-in-out infinite alternate 3s;
            width: 100%;
            height: 100%;
        }
    }
    &__decor-three {
        position: absolute;
        z-index: 1;
        @include responsive(bottom, -117, -117, -56);
        @include responsive(left, 488, 56, 34);
        @include responsive(width, 194, 194, 88);
        @include responsive(height, 158, 158, 74);
        pointer-events: none;
        img {
            animation: levitateTwo 10s ease-in-out infinite alternate 5s;
            width: 100%;
            height: 100%;
        }
    }
    &__decor-four {
        position: absolute;
        z-index: 1;
        @include responsive(bottom, 238, 309, 124);
        @include responsive(right, 288, -77, -40);
        @include responsive(width, 114, 114, 57);
        @include responsive(height, 91, 91, 45.5);
        pointer-events: none;
        img {
            animation: levitateTwo 10s ease-in-out infinite alternate 2s;
            width: 100%;
            height: 100%;
        }
    }
    &__decor-five {
        position: absolute;
        @include responsive(bottom, -125, -125, -76.6);
        @include responsive(right, 398, -130, -65);
        @include responsive(width, 612, 612, 306);
        @include responsive(height, 172, 172, 86);
        pointer-events: none;
        img {
            animation: levitateTwo 10s ease-in-out infinite alternate 5s;
            width: 100%;
            height: 100%;
        }
    }
}

.team-slide {

    &__img {
        position: relative;
        @include responsive(width, 562, 336.5, 160);  
        margin-left: auto;
        margin-right: auto; 
        img {
            width: 100%;
        }
    }

    &__img-glitch {
    }

    &__name {
        position: relative;
        z-index: 1;
        font-family: var(--font3);
        font-weight: 400;
        font-size: 80px;
        @include responsive(font-size, 80, 58, 36);
        line-height: 105%;
        text-align: center;
        color: var(--bodi-2);
        text-shadow: 0 0 16px #ff029a, 0 4px 4px  #e32e9b;
        transform: rotate(4deg);
        @include responsive(margin-top, -66, -41, -29);
        @include responsive(margin-bottom, 24, 16, 8);
        @media(max-width:480px){
            transform: rotate(2deg);
        }
    }

    &__prof {
        @include responsive(font-size, 32, 32, 18);
        text-align: center;
        @include responsive(margin-bottom, 24, 24, 4);
    }

    &__social {
        position: relative;
        z-index: 2;
        display: flex;
        justify-content: center;
    }
}

@keyframes lightPulse {
    0% {
      opacity: 1; /* Яркое свечение */
    }
    10% {
        opacity: 1; /* Яркое свечение */
    }
    11% {
        opacity: 0.5;
    }
    12% {
        opacity: 0.8;
    }
    30% {
        opacity: 0.8; /* Яркое свечение */
    }
    31% {
        opacity: 0;
    }
    32% {
        opacity: 0.6;
    }
    50% {
        opacity: 0.6; /* Яркое свечение */
    }
    51% {
        opacity: 0;
    }
    52% {
        opacity: 1;
    }
    70% {
        opacity: 1;
    }
    71% {
        opacity: 0.3;
    }
    72% {
        opacity: 0.7;
    }
    80% {
        opacity: 0.7;
    }
    81% {
        opacity: 0.3;
    }
    84% {
        opacity: 0.3;
    }
    85% {
        opacity: 0.6;
    }
    86% {
        opacity: 0.2;
    }
    99% {
        opacity: 0.2;
    }
    100% {
        opacity: 1;
    }
  }
  

  @keyframes levitation {
    0%, 100% {
      transform: translateY(0); /* Начальная позиция */
    }
    50% {
      transform: translateY(-8px); /* Легкое смещение вверх */
    }
  }

  @keyframes levitateTwo {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(10px, -10px); /* Перемещение вправо и вверх */
  }
  50% {
    transform: translate(0, -20px); /* Вернуться в центр и подняться выше */
  }
  75% {
    transform: translate(-10px, -10px); /* Перемещение влево и вниз */
  }
  100% {
    transform: translate(0, 0); /* Вернуться в начальное положение */
  }
}