.contacts {
    position: relative;
    // z-index: 1;
    @include responsive(padding-top, 200, 130, 60);
    @include responsive(min-height, 1409, 1995, 1045);
    &::before {
        position: fixed;
        content: '';
        width: 100%;
        height: 30%;
        background-color: hsl(274, 77%, 12%);
        bottom:0;
        left: 0;

        transition: all 0.3s ease-in-out  0s;
        opacity: 0;
    }

    &.start-bg::before {
        opacity: 1;
        transition: all 1.3s ease-in-out  0s;
    }
    &__bg {
        transition: all 1s ease-in-out  0s;
        opacity: 0;
        @include responsive(top, 240, 160, 80);
        .start-bg & {
            opacity: 1;
        }
    }
    &__container {
        position: relative;
        z-index: 1;
        @include responsive(padding-top, 40, 30, 20);
    }

    &__title {
        display: flex;
        flex-direction: column;
        // text-align: center;
        @include responsive(font-size, 66, 56, 28);
        line-height: 120%;
        max-width: vw(1160);
        margin-left: auto;
        margin-right: auto;
        text-transform: uppercase;
        @include responsive(margin-bottom, 80, 60, 36);
        p {
            display: inline-block;
            margin-left: auto;
        }
        @media(max-width:1024px){
            max-width: 100%;

            p {
                margin-top: vw(-68, 768);
                display: flex;
                flex-direction: column;
                align-items: end;
                text-align: right;
            }
        }
        @media(max-width:480px){
            p {
                margin-top: vw(-34, 360);
            }
        }
    }

    &__row {
        display: flex;
        @include responsive(gap, 67, 60, 24);

        @media(max-width:1024px){
            flex-direction: column;
        }
    }

    &__form {
    }

    &__auto {
        position: relative;
        @include responsive(width, 831, 696, 328);
        margin-top: vw(258);
        @media(max-width:1024px){
            margin-top: 0;
            margin-left: auto;
            margin-right: auto;
        }

        img {
            width: 100%;
        }

        transition: all 1s ease-in-out  0s;
        opacity: 0;
        .start-bg & {
            opacity: 1;
        }
    }

    &__auto-light {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        animation: blinkLights 1s infinite alternate;
        img {
            width: 100%;

        }
    }

		&__auto-dialog {
            position: absolute;
            top: 0;
            @include responsive(left, 335, 301, 136);
            @include responsive(width, 140, 98, 56);
            @include responsive(height, 148, 104, 60);
            animation: shakeAnimation 3.5s ease-in-out 1.5s infinite;
            img {
                width: 100%;
            }
		}
}


.contacts-form {
    position: relative;
    width: vw(862);
    @include responsive(height, 924, 1020, 582);
    @media(max-width:1024px){
        width: 100%;
    }
    @media(max-width:480px){
        width: 100%;
    }
    &__bg {
    }

    &__body {
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 1;
        @include responsive(padding-top, 80, 80, 40);
        @include responsive(padding-bottom, 90, 86, 44);
        @include responsive(padding-left, 44, 36, 16);
        @include responsive(padding-right, 44, 36, 16);
        @include responsive(gap, 24, 24, 16);
        height: 100%;


        button {
            opacity: 0.6;
            pointer-events: none;
        }
        &.unlock button {
            opacity: 1;
            pointer-events: all;
        }
    }

    &__title {
        text-align: center;
        @include responsive(font-size, 44, 44, 24);
        line-height: 120%;
        text-transform: uppercase;
    }

    &__input-row {
        display: flex;
        flex-direction: column;
        @include responsive(gap, 16, 16, 8);
    }

    &__item-input {
    }

    &__input {
    }

    &__item-row {
        display: grid;
        grid-template-columns: 1fr 1fr;
        @include responsive(gap, 16, 16, 8);

        .text-input {
            clip-path: polygon(
                0% 30%,     /* Нижний левый скос */
                6.3% 0,     /* Верхний левый угол */
                100% 0,     /* Верхний правый угол */
                100% 70%,   /* Правый верхний внутренний угол перед скосом */
                93.7% 100%, /* Нижний правый скошенный угол */
                0% 100%     /* Нижний левый угол */
            );
            @media(max-width:1024px){
                clip-path: polygon(
                    0% 30%,      /* Начальная точка/шаг вверху */
                    3.85% 0,     /* Верхний левый угол */
                    100% 0,      /* Верхний правый угол */
                    100% 70%,    /* Правая верхняя точка перед скосом */
                    96.15% 100%, /* Нижний правый скошенный угол */
                    0% 100%      /* Нижний левый угол */
                );
            }
            @media(max-width:480px){
                clip-path: polygon(
                    0% 27.27%,    /* Начальная точка/шаг вверху */
                    4.05% 0,      /* Верхний левый угол */
                    100% 0,       /* Верхний правый угол */
                    100% 72.73%,  /* Правая верхняя точка перед скосом */
                    95.95% 100%,  /* Нижний правый скошенный угол */
                    0% 100%       /* Нижний левый угол */
                );
            }
        }
        
        @media(max-width:768px){
            grid-template-columns: 1fr;
        }
    }

    &__textarea {
        @include responsive(height, 248, 248, 130);
    }

    &__privat-row {
        display: flex;
        justify-content: space-between;
        @include responsive(gap, 24, 24, 8);
        // flex-wrap: wrap;

        @media(max-width:480px){
            flex-direction: column;
        }
    }

    &__caph {
        height: rem(76);
        margin-top: vw(5);
        
        
        @media(max-width:1570px){
            width: rem(234);
            height: rem(56);
            margin-top: vw(10);
            .g-recaptcha {
                transform-origin: 0 0; 
                transform: scale(0.77);
            }
        }
        @media(max-width:1380px){
            margin-top: vw(5);
        }
        @media(max-width:1210px){
            width: rem(194);
            margin-top: vw(5);
            .g-recaptcha {
                transform-origin: 0 0; 
                transform: scale(0.65);
            }
        }
        @media(max-width:1024px){
            width: rem(260);
            margin-top: 0;
            .g-recaptcha {
                transform-origin: 0 0; 
                transform: scale(0.85);
            }
        }
         @media(max-width:640px){
            width: rem(194);
            // margin-top: vw(5);
            .g-recaptcha {
                transform-origin: 0 0; 
                transform: scale(0.65);
            }
        }
        @media(max-width:480px){
            display: flex;
            justify-content: center;
            width: auto;
            height: auto;
            // margin-top: vw(5);
            .g-recaptcha {
                transform-origin: 0 0; 
                transform: scale(0.7);
                width: rem(212);
                height: rem(54);
            }
        }
    }

    &__privat-text {
        max-width: vw(395);
     
        p {
            @include responsive(font-size, 20, 16, 12);
        }
        a {
            text-decoration: underline;
            text-decoration-skip-ink: none;
            color: var(--bodi-1);
            transition: all 0.3s ease  0s;
            &:hover {
                opacity: 0.7;
            }
        }
        @media(max-width:1024px){
            max-width: 100%;
        }
    }
    &__button-row {
        display: flex;
        justify-content: center;
        // @include responsive(margin-top, 16, 16, 8);
        margin-top: auto;
    }
    &__button {
        background: none;
        
    }

}





@keyframes blinkLights {
    0% {
        opacity: 1; /* Фары включены */
    }
    100% {
        opacity: 0; /* Фары выключены */
    }
}