.quest {
    @include responsive(padding-top, 240, 160, 90);

    &__container {
    }

    &__row {
        display: flex;
        @include responsive(gap, 36, 36, 16);
        flex-direction: column;
        width: vw(1160);
        margin-left: auto;
        margin-right: auto;
        @media(max-width:1024px){
            width: 100%;
        }
    }

    &__item {
    }

    &__title {
        @include responsive(margin-bottom, 80, 60, 36);
        @include responsive(font-size, 174, 120, 60);
    }
}
.title-bul {

    &__text {
    }

    &__double {
    }
}

.item-quest {
    cursor: pointer;
    &._active {
        .item-quest__title {
            color: var(--bodi-2);
            text-shadow: 0 0 6px #ff029a, 0 -1px 3px #e32e9b;
        }
        .line-two {
            transform: rotate(0);
        }
    }
    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        @include responsive(gap, 40, 40, 0);
        @include responsive(height, 118, 170, 90);
        @include responsive(padding-top, 29, 40, 20);
        @include responsive(padding-bottom, 29, 32, 12);
        @include responsive(padding-left, 45, 36, 16);
        @include responsive(padding-right, 36, 36, 16);
        z-index: 2;
    }

    &__bg {
        
    }

    &__title {
        
        position: relative;
        z-index: 1;
        @include responsive(font-size, 44, 44, 23);
        transition: all 0.3s ease  0s;
        text-transform: uppercase;
        line-height: 105%;
        margin-top: vw(12);
        @media(max-width:1024px){
            margin-top: 0;
            line-height: 110%;
        }
        @media(max-width:480px){
            line-height: 120%;
        }
    }   

    &__button {
        position: relative;
        z-index: 1;
       
    }

    &__body {
        position: relative;
        @include responsive(font-size, 32, 32, 18);
        
        
        @include responsive(margin-top, -20, -20, -11);
        opacity: 0;
        max-height: 0;
        pointer-events: none;
        transition: all 0.3s ease  0s;
        
        // &::before {
        //     position: absolute;
        //     content: '';
        //     width: 100%;
        //     height: 100%;
            
        //     top:0;
        //     left: 0;
        //     transition: all 0.3s ease  0s;
            
        // }
        background: linear-gradient(79deg, rgba(44, 26, 95, 0.01) 0%, rgba(185, 34, 161, 0.01) 50%, rgba(143, 22, 167, 0.01) 72.5%);
        backdrop-filter: blur(36px);
        clip-path: polygon(
                0% 0%,   /* Вершина верхнего левого угла */
                100% 0%, /* Вершина верхнего правого угла */
                100% calc(100% - 24px), /* Скошенная вершина внизу справа */
                calc(100% - 24px) 100%, /* Нижний правый угол */
                24px 100%,     /* Нижний левый угол */
                0% calc(100% - 24px)  /* Скошенная вершина внизу слева */
            );
        @media(max-width:480px){
            clip-path: polygon(
                0% 0%,   /* Вершина верхнего левого угла */
                100% 0%, /* Вершина верхнего правого угла */
                100% calc(100% - 12px), /* Скошенная вершина внизу справа */
                calc(100% - 12px) 100%, /* Нижний правый угол */
                12px 100%,     /* Нижний левый угол */
                0% calc(100% - 12px)  /* Скошенная вершина внизу слева */
            );
        }

        p {
            position: relative;
            z-index: 1;
        }

        ._active & {
            max-height: 1000px;
            @include responsive(padding-top, 49, 44, 23);
            @include responsive(padding-bottom, 29, 36, 16);
            pointer-events: all;
            opacity: 1;
            
        }
    }

    &__text {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: max-content;
        @include responsive(padding-left, 45, 36, 16);
        @include responsive(padding-right, 36, 36, 16);
        @include responsive(padding-top, 49, 44, 23);
        @include responsive(padding-bottom, 29, 36, 16);

        ._active & {
            opacity: 1;
        }
    }
}

.btn-item-quest {
    display: flex;
    justify-content: center;
    align-items: center;
    @include responsive(width, 96, 96, 40);
    @include responsive(min-width, 96, 96, 40);
    @include responsive(height, 92, 92, 38.33);
    @include responsive(margin-right, -5, -5, -5);
    margin-top: vw(6);
    @media(max-width:1024px){
        margin-top: 0;
    }
    @media(max-width:480px){
        margin-bottom: vw(4, 360);
    }
    svg {
        width: 100%;
        height: auto;
    }

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    &__line {
        z-index: 1;
        background-color:  var(--bodi-1);
        @include responsive(width, 24, 24, 12);
        @include responsive(height, 4, 4, 2);
        box-shadow: 0 0 3px 0 #ff029a;

        &.line-two {
            position: absolute;
            transform: rotate(90deg);
            top: auto;
            left: auto;
            transition: all 0.3s ease  0s;
        }
    }
}
