.footer {
  position: relative;
  z-index: 0;
  padding: rem(20) 0;
  // background-color: hsl(274, 77%, 12%);
  @include responsive(margin-top, 80, -100, -65);
  @include responsive(padding-bottom, 80, 60, 36);
  // .footer__container
  .warning-wrapper & {
    @include responsive(margin-top, -280, -300, -85);
  }
  .wrapper-cases-page & {
    margin-top: 0 !important;
  }
  &__container {
    position: relative;
    // z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    // @include responsive(gap, 24, 24, 12);
  }

  &__title {
    @include responsive(font-size, 80, 58, 36);
    text-shadow: 0 0 16px #ff029a, 0 4px 4px  #e32e9b;
    transform: rotate(3deg);
    line-height: 120%;
    color: var(--bodi-2);
    font-family: var(--font3);
  }

  .header__social-link {
    @include responsive(width, 96, 96, 56);
    margin: 0;
  }
}
