@keyframes show-header {
  0% {
    top: -100%;
  }

  100% {
    top: 0;
  }
}

@keyframes fade-up-big {
  0% {
    transform: translateY(1rem);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes gradientShift {
            
  0% {
     filter: hue-rotate(0deg);
     opacity: 0;
  }
  6% {
    opacity: 1;
  }
  25% {
      filter: hue-rotate(30deg);
      opacity: 0.5;
  }
  50% {
      filter: hue-rotate(20deg);
      opacity: 1;
  }
  75% {
      filter: hue-rotate(20deg);
      opacity: 0.7;
  }
  100% {
      filter: hue-rotate(0deg);
  }

}

@keyframes fade {
  0% {
      opacity: 1;
      // transform: translateX(-0%);
  }
  20% {
      opacity: 0.4;
      // transform: translateX(5%);
  }
  40% {
      opacity: 1;
      // transform: translateX(-0%);
  }
  100% {
      opacity: 1;
      // transform: translateX(-0%);
  }
}





@keyframes drive {
  0% {
      transform: translateX(50%);
  }
  100% {
      transform: translateX(0);
  }
}

@keyframes rotationDrive {
  from {
      transform: rotate(0);
  }
  to {
      transform: rotate(-360deg);
  }
}

@keyframes decorAuto{
  from {
      opacity: 0;
  }
  70% {
      opacity: 1;
      transform: scale(1.2);
  }
  to {
      opacity: 1;
      transform: scale(1);
  }
}

@keyframes smokeAnimation {
  0% {
      transform: scale(1);
      opacity: 1;
  }
  10% {
      opacity: 0.8;
      transform: scale(1.05);
  }
  20% {
      opacity: 0.9;
      transform: scale(1);
  }
  30% {
      opacity: 0.75;
      transform: scale(0.90) ;
  }
  40% {
      opacity: 1;
      transform: scale(1) ;
  }
  50% {
      opacity: 0.8;
      transform: scale(0.95);
  }
  60% {
      opacity: 0.9;
      transform: scale(1) ;
  }
  70% {
      opacity: 0.7;
      // transform: scale(0.96) translateX(1%);
  }
  80% {
      opacity: 1;
      // transform: scale(0.98) translateX(0.5%);
  }
  90% {
      opacity: 0.8;
      transform: scale(1);
  }
  100% {
      opacity: 0;
      transform: scale(1);
  }
}

@keyframes shakeAnimation {
  0%, 100% {
      transform: rotate(0deg);
  }
  5% {
      transform: rotate(-1deg);
  }
  10% {
      transform: rotate(2deg);
  }
  15% {
      transform: rotate(-2deg);
  }
  20% {
      transform: rotate(1deg);
  }
  25% {
      transform: rotate(0deg);
  }
  /* Пустое пространство в конце анимации до 4 секунд */
}