.cases-page {
    @include responsive(padding-top, 80, 80, 36);
    @include responsive(padding-bottom, 100, 60, 44);
    &__container {
        max-width: vw(1320);

        @media(max-width:1024px){
            max-width: 100%;
        }
    }

    &__title {
        text-transform: uppercase;
        @include responsive(font-size, 66, 56, 28);
        @include responsive(margin-bottom, 36, 36, 24);
        text-align: center;
        line-height: 120%;
    }
    h2 {
        text-align: center;
        text-transform: uppercase;
        @include responsive(font-size, 44, 44, 24);
        @include responsive(margin-bottom, 16, 16, 12);
        line-height: 120%;
    }
    &__text {
        @include responsive(margin-bottom, 36, 36, 24);
        p {
            @include responsive(font-size, 32, 32, 16);
            line-height: 120%;
        }
    }

    &__img {
        @include responsive(margin-bottom, 36, 36, 24);
        img {
            width: 100%;
        }
    }

    &__button-row {
            display: flex;
            justify-content: center;
    }

    &__button {
    }
}
