.marquee {
    overflow: hidden;
    white-space: nowrap;
    user-select: none;

    &__text {
        display: inline-block;
        width: auto;
        counter-reset: item;
        white-space: nowrap;
    }
}

.noscroll {
    overflow: hidden;
    height: 100%;
}


.swiper-pagination {
   

    .swiper-pagination-bullet {
        @include responsive(width, 120, 80, 44);
        @include responsive(height, 12, 12, 6);
        border-radius: 0;
        background: linear-gradient(359deg, #261332 0%, #412065 100%);
        box-shadow: 0 0 6px 0 #5f3a8a, inset 0 2px 3px 0 #1f1431;
        opacity: 1;
        transition: all 0.3s ease  0s;

        &:nth-child(1) {
            clip-path: polygon(0% 0%, 91.67% 0%, 100% 100%, 8.33% 100%);
        }
        &:nth-child(2) {
            clip-path: polygon(0% 0%, 100% 0%, 90% 100%, 10% 100%);
        }

        &:nth-child(3) {
            clip-path: polygon(0% 0%, 91.67% 0%, 100% 100%, 8.33% 100%);
            transform: scaleY(-1);
        }
    }
    .swiper-pagination-bullet-active {
        background: rgb(163,129,250);
        box-shadow: 0 0 16px  #2602ff;
    }
}

