.text-input {
    background-color: var(--bodi-3);
    border: 1px solid var(--bodi-3);
    width: 100%;
    @include responsive(padding-top, 16, 16, 9.2);
    @include responsive(padding-bottom, 16, 16, 9.2);
    @include responsive(padding-left, 24, 24, 12);
    @include responsive(padding-right, 24, 24, 12);
    font-weight: 400;
    @include responsive(font-size, 32, 32, 16);
    line-height: 120%;
    color: #fff;
    transition: all 0.3s ease  0s;
    // opacity: 0.6;

    clip-path: polygon(
        0% 30%,    /* Левый нижний угол, наклонен вверх */
        3% 0%,     /* Верхний левый угол */
        100% 0%,   /* Верхний правый угол */
        100% 70%,  /* Соединение с внутренним углом*/
        97% 100%,  /* Нижний правый внутренний угол */
        0% 100%    /* Нижний левый угол */
    );
    @media(max-width:1024px){
        clip-path: polygon(
            0% 30%,      /* Начальная точка/шаг вверху */
            3.85% 0,     /* Верхний левый угол */
            100% 0,      /* Верхний правый угол */
            100% 70%,    /* Правая верхняя точка перед скосом */
            96.15% 100%, /* Нижний правый скошенный угол */
            0% 100%      /* Нижний левый угол */
        );
    }
    @media(max-width:480px){
        clip-path: polygon(
            0% 27.27%,    /* Начальная точка/шаг вверху */
            4.05% 0,      /* Верхний левый угол */
            100% 0,       /* Верхний правый угол */
            100% 72.73%,  /* Правая верхняя точка перед скосом */
            95.95% 100%,  /* Нижний правый скошенный угол */
            0% 100%       /* Нижний левый угол */
        );
    }

    &._error {
        color: rgb(156, 0, 0);
    }
    &._error::placeholder {
        color: rgb(156, 0, 0);
    }
}
textarea.text-input {
    clip-path: polygon(
        0% 9.68%,     /* Начальная точка/шаски вверху */
        3.1% 0,       /* Верхний левый угол */
        100% 0,       /* Верхний правый угол */
        100% 90.32%,  /* Правая верхняя точка перед скосом */
        96.9% 100%,   /* Нижний правый скошенный угол */
        0% 100%       /* Нижний левый угол */
