    @media(max-width:1024px){
        clip-path: polygon(
            0% 9.68%,     /* Начальная точка/шаг вверху */
            3.85% 0,      /* Верхний левый угол */
            100% 0,       /* Верхний правый угол */
            100% 90.32%,  /* Правая верхняя точка перед скосом */
            96.15% 100%,  /* Нижний правый скошенный угол */
            0% 100%       /* Нижний левый угол */
        );
    }
    @media(max-width:480px){
        clip-path: polygon(
            0% 9.23%,    /* Начальная точка/шаг вверху */
            4.05% 0,     /* Верхний левый угол */
            100% 0,      /* Верхний правый угол */
            100% 90.77%, /* Правая верхняя точка перед скосом */
            95.95% 100%, /* Нижний правый скошенный угол */
